import React from 'react';
import {Panel, Div, Button, Cell, Avatar, ModalRoot, ModalPage, ModalPageHeader, HeaderButton, IS_PLATFORM_IOS, IS_PLATFORM_ANDROID} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import VKStories from "vk-stories";
import connect from "@vkontakte/vkui-connect-promise";
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Done from '@vkontakte/icons/dist/24/done';
VKStories.init(connect);




//компонент модального окна
class Modal extends React.Component {
  constructor(props) {
      super(props);
  }

    ReturnMap = () => {
      this.props.theStory();
    }

  GenerateStory = () => {
      VKStories.generateStoryFromTemplate(require("../img/Design-story.png"), [
      {
        font: "45px Arial",
        align: "center",
        color: "#FFFFFF",
        value: "ТЕСТ ИСТОРИИ",
        x: 540,
        y: 1133,
        }
     ])
     .then( (story) => {
        VKStories.shareStory(7150376, story, { add_to_news: true })
        this.ReturnMap();
    })
}




  render() {
    return(
      <ModalRoot activeModal={'id'}>
<ModalPage
id={'id'}
onClose={this.q}
dynamicContentHeight={true}
settlingHeight={50}
header={
<ModalPageHeader left={<HeaderButton onClick={this.props.cancelHistory}><Icon24Cancel /></HeaderButton>}
                 right={<HeaderButton onClick={this.props.sendHistory}>{IS_PLATFORM_IOS ? 'Готово' : <Icon24Done />}</HeaderButton>}>
Поделиться в истории
</ModalPageHeader>
}
>
<Cell>ffffffffffffffffffffffhhhhhhhhhhhhhhhhhhhhhh
hhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhh
hhhhhhhhhhhhhhhhhhhhhhhhhhhh</Cell>
</ModalPage>
</ModalRoot>
   );
  }

}

export default Modal;
